.feedback {
  position: absolute;
  width: 300px;
  height: 200px;
  background-color: white;
  border-radius: 10px;
  border: 1px solid black;
  display: none;
  z-index: 1000;
}

.feedback-header-wrap {
  padding-top: 5%;
  width: 80%;
  margin: auto;
  margin-bottom: 5%;
}

.feedback-text {
  display: inline;
  text-align: center;
  font-size: 20px;
}

.icon-feedback {
  margin-right: 4%;
}

.feedback-icons {
  width: 80%;
  margin: auto;
  margin-bottom: 3%;
}

.feedback-icon-wrap {
  display: inline;
  cursor: pointer;
}

.feedback-icon {
  display: inline;
}


.feedback-icon-text {
  font-size: 15px;
  margin-left: 2%;
  margin-right: 6%;
}

.feedback-memo {
  
  display: block;
  margin-top: 1.2%;
  margin: auto;
  width: 80%;
  height: 30%;
  resize: none;
  margin-bottom: 5%;
}

.feedback-done-wrap {
  width: 80%;
  height: 10%;
  margin: auto;
}

.feedback-done {
  font-size: 18px;
  text-align: center;
  display: block;
  float: right;
  width: 25%;
  border-radius: 10px;
}

